/* utility classes */
ul.flat {
  list-style: none;
  margin: 0;
}
a.icon {
  display: block;
  position: relative;
  padding-left: 2rem;
}
a.icon svg {
  position: absolute;
  width: 1.75rem;
  left: 0rem;
  top: -0.5rem;
}

/* https://www.a11yproject.com/posts/2013-01-11-how-to-hide-content/ */
.visually-hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}
